"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { useEffect, useMemo, useRef, useState } from "react";

type Item = { href: string; label: string };

const items: Item[] = [
  { href: "/markets", label: "Markets" },
  { href: "/trade", label: "Trade" },
  { href: "/orders", label: "Orders" },
  { href: "/wallet", label: "Wallet" },
  { href: "/account", label: "Account" },
];

export function MobileNav({ basePath = "" }: { basePath?: string }) {
  const pathname = usePathname() ?? "";
  const [unread, setUnread] = useState<number>(0);
  const esRef = useRef<EventSource | null>(null);

  const accountHref = useMemo(() => `${basePath}/account`, [basePath]);

  useEffect(() => {
    let alive = true;

    const load = async () => {
      if (document.visibilityState !== "visible") return;
      try {
        const res = await fetch("/api/notifications?limit=1", { cache: "no-store", credentials: "include" });
        const json = (await res.json().catch(() => null)) as any;
        if (!res.ok) {
          if (alive) setUnread(0);
          return;
        }
        const n = Number(json?.unread_count ?? 0);
        if (alive) setUnread(Number.isFinite(n) && n > 0 ? Math.trunc(n) : 0);
      } catch {
        if (alive) setUnread(0);
      }
    };

    const stopSse = () => {
      try {
        esRef.current?.close();
      } catch {
        // ignore
      }
      esRef.current = null;
    };

    const startSse = () => {
      if (typeof window === "undefined") return;
      if (document.visibilityState !== "visible") return;
      if (esRef.current) return;
      try {
        const es = new EventSource("/api/notifications/stream", { withCredentials: true } as any);
        es.addEventListener("notification", () => {
          if (!alive) return;
          setUnread((n) => Math.min(999, (Number.isFinite(n) ? n : 0) + 1));
        });
        es.addEventListener("ready", () => {
          // resync count on connect
          void load();
        });
        es.onerror = () => {
          // Let it retry automatically; still keep polling for correctness.
        };
        esRef.current = es;
      } catch {
        // ignore; polling will still work
      }
    };

    void load();
    startSse();
    const id = window.setInterval(load, 25_000);
    const onVis = () => {
      if (document.visibilityState === "visible") {
        startSse();
        void load();
      } else {
        stopSse();
      }
    };
    document.addEventListener("visibilitychange", onVis);

    return () => {
      alive = false;
      window.clearInterval(id);
      document.removeEventListener("visibilitychange", onVis);
      stopSse();
    };
  }, []);

  return (
    <nav
      className="fixed bottom-0 left-0 right-0 z-50 border-t border-[var(--border)] bg-[var(--card)]/95 backdrop-blur"
      style={{ paddingBottom: "env(safe-area-inset-bottom)" }}
      aria-label="Primary"
    >
      <div className="mx-auto grid max-w-xl grid-cols-5 gap-1 px-2 py-2">
        {items.map((it) => {
          const href = `${basePath}${it.href}`;
          const active = pathname === href || pathname.startsWith(`${href}/`);
          const showBadge = href === accountHref && unread > 0;
          return (
            <Link
              key={href}
              href={href}
              className={
                "relative flex h-11 flex-col items-center justify-center rounded-xl px-1 text-[11px] font-semibold " +
                (active
                  ? "bg-[var(--card-2)] text-[var(--foreground)]"
                  : "text-[var(--muted)] hover:bg-[var(--card-2)]/60")
              }
            >
              <span className="leading-none">{it.label}</span>
              {showBadge ? (
                <span
                  className="absolute right-2 top-2 min-w-[16px] rounded-full border border-[var(--border)] bg-[var(--warn-bg)] px-1 text-[10px] font-extrabold leading-[14px] text-[var(--warn)]"
                  aria-label={`${unread} unread notifications`}
                >
                  {unread > 99 ? "99+" : String(unread)}
                </span>
              ) : null}
            </Link>
          );
        })}
      </div>
    </nav>
  );
}
